


































































































































































@import '@design';

.navbar {
  color: white;
  margin-bottom: 25px;
  position: relative;
  text-align: center;
  &__back {
    position: absolute;
    left: 0;
    top: 50%;
    border: solid white;
    border-width: 0 3px 3px 0;
    display: inline-block;
    border-radius: 2px;
    padding: 5px;
    transform: translateY(-50%) rotate(135deg);
    box-shadow: 10px 10px 35px rgba(#f19040, 0.3);
  }
}
.card {
  &__list {
    max-width: 344px;
    margin: auto;
  }
  &__item {
    position: relative;
    text-align: left;
    &:not(:first-child) {
      margin-top: -100px;
    }
  }
  &__info {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    padding: 20px;
    box-sizing: border-box;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__radio {
    position: absolute;
    opacity: 0;
    &:checked + .card__label {
      &:before {
        background-color: $color-primary;
      }
      &:after {
        transform: scale(1) rotate(45deg);
      }
    }
  }
  &__label {
    position: relative;
    cursor: pointer;
    &:before {
      content: '';
      margin-right: 10px;
      display: inline-block;
      width: 20px;
      height: 20px;
      border: 1px solid white;
      border-radius: 50%;
      vertical-align: middle;
      box-sizing: border-box;
    }
    &:after {
      content: '';
      position: absolute;
      left: 5px;
      top: 50%;
      background: white;
      width: 2px;
      height: 2px;
      box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white,
        4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
      transform: scale(0) rotate(45deg);
      transition: transform 0.25s linear;
    }
  }
}

.add-card {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top-right-radius: 40px;
  border-top-left-radius: 40px;
  background-color: rgba(white, 0.8);
  box-shadow: 10px 10px 35px rgba(#f19040, 0.3);
  backdrop-filter: blur(10px);
  text-align: center;
  padding: 25px 40px;
  box-sizing: border-box;
}
.button--add {
  margin-top: 25px;
}
.button--pay {
  position: fixed;
  bottom: 35px;
  left: 50%;
  transform: translateX(-50%);
  &:not(:disabled) {
    background: white;
    color: $color-primary;
  }
}
.form {
  text-align: left;
  &__item {
    padding: 15px 0;
    border-bottom: 1px solid #979797;
    flex: 1;
    label {
      display: block;
      margin-bottom: 10px;
      color: #4a4a4a;
    }
  }
  &__split {
    display: flex;
    .form__item:last-child {
      border-left: 1px solid #979797;
      padding-left: 15px;
    }
  }
}

input {
  width: 100%;
  background: transparent;
  outline: none;
  border: none;
  &::placeholder {
    color: rgba(74, 74, 74, 0.3);
  }
}
