
































@import '@design';
.message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;

  &__image {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
    z-index: 1;
    width: 50%;
  }
  &__body {
    margin-top: 25%;
    margin-bottom: 75px;
    padding: 10px 25px;
    box-sizing: border-box;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    background-color: rgba(white, 0.8);
    backdrop-filter: blur(10px);
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__meta {
    color: $color-primary-light;
  }
}
